import React, {useState} from 'react';
import './Product.css';
import { useStateValue } from './StateProvider';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';

function Product({ id, title, price, rating, image }) {

    const [{ basket }, dispatch] = useStateValue();
    const [counterInput, setCounterInput] = useState('1');

    console.log(counterInput)
    const onInputChange = (e) => {
        setCounterInput(e.target.value);
    }

    const plusBtnChange = () => {
        setCounterInput(parseInt(counterInput) + 1);
    }

    const minusBtnChange = () =>{
        setCounterInput(parseInt(counterInput) - 1);
    }

    const addToBasket = () => {
        console.log(counterInput)
        // Add item to basket
        Array(counterInput).fill().map((c) => dispatch({
            type: 'ADD_TO_BASKET',
            item: {
                id: id,
                title: title,
                price: price,
                rating: rating,
                image: image
            }
        }))      
    }

    return (
        <div className="product">
            <div className="product__info">
                <p>{title}</p>
                <p className="product__price">
                    <small>$</small>
                    <strong>{price}</strong>
                </p>
                <div className="product__rating">
                    {
                        // noric nayi kjoges   
                        Array(rating)
                            .fill()
                            .map((s) => (
                                <p>⭐</p>
                            ))
                    }
                </div>
            </div>
            <img src={image} alt="" />
            <button onClick={addToBasket}>Add to basket</button>
            <div className="counter">
                <RemoveIcon onClick={minusBtnChange} className="counter__minus"/>
                <input type="number" value={counterInput} min="0" max="50"  onChange={onInputChange} className="counter__input" />
                <AddIcon onClick={plusBtnChange} className="counter__plus"/>
            </div>
        </div>
    );
}

export default Product;
