import React, { useState } from 'react';
import './CheckoutProduct.css';
import { useStateValue } from './StateProvider';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';


function CheckoutProduct({ id, title, image, price, rating }) {
    const [{ basket }, dispatch] = useStateValue();
    const [counterInput, setCounterInput] = useState('1');

    const removeFromBasket = () => {
        //remove item from basket
        dispatch({
            type: "REMOVE_FROM_BASKET",
            id: id
        });
    }

    const onInputChange = (e) => {
        setCounterInput(e.target.value);
    }

    const plusBtnChange = () => {
        setCounterInput(parseInt(counterInput) + 1);
    }

    const minusBtnChange = () => {
        setCounterInput(parseInt(counterInput) - 1);
    }

    

    return (
        <div className="checkoutProduct">
            <img className="checkoutProduct__image" src={image} alt="" />

            <div className="checkoutProduct__info">
                <p className="checkoutProduct__title">{title}</p>
                <p className="checkoutProduct__price">
                    <small>$</small>
                    <strong>{price}</strong>
                </p>

                <div className="checkoutProduct__rating">
                    {
                        // noric nayi kjoges   
                        Array(rating)
                            .fill()
                            .map((s) => (
                                <p>⭐</p>
                            ))
                    }
                </div>
                <button onClick={removeFromBasket}>Remove from basket</button>
                <div className="counter">
                    <RemoveIcon onClick={minusBtnChange} className="counter__minus" />
                    <input type="number" value={counterInput} min="0" max="50" onChange={onInputChange} className="counter__input" />
                    <AddIcon onClick={plusBtnChange} className="counter__plus" />
                </div>
            </div>
        </div>
    )
}

export default CheckoutProduct;
